<template>
  <div>
    <div class="tabWrap">
      <table class="kwTable">
        <tbody>
          <tr>
            <th width="200">模板名称</th>
            <th>路径</th>
            <th class="f-tac"
                width="100">状态</th>
          </tr>
          <tr v-for="(item,index) in tableData"
              :key="index">
            <td>{{item.template_name}}</td>
            <td>{{item.path}}</td>
            <td class="f-tac">{{item.status==1?'可采集':'不可采集'}}</td>
          </tr>
        </tbody>
      </table>
      <div class="pages">
        <a-pagination v-model="current"
                      :pageSize="pageSize"
                      :total="total"
                      @change="onChange" />
      </div>
    </div>
  </div>
</template>

<script>
import $axios from '../../../../utils/request.js'
export default {
  name: 'TempMge',
  data () {
    return {
      tableData: [],
      current: 1,
      pageSize: 10,
      total: 0,
      page: 1,
      status: 1
    }
  },
  mounted () {
    this.tableDataFn()
  },
  methods: {
    tableDataFn () {
      let url = `${this.baseURL}/template/select?page=${this.page}&limit=${this.pageSize}&status=${this.status}`
      console.log($axios)
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          this.tableData = res.data.data
          this.total = res.data.count
        }
      })
    },
    onChange (current) {
      this.current = current
      this.page = current
      this.tableDataFn()
    }
  }
}
</script>
<style scoped>
.tabWrap {
  padding: 20px 16px 0 16px;
}
.kwTable {
  width: 100%;
}
.kwTable th {
  padding: 16px 16px;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  text-align: left;
  background: #fafafa;
  border: 1px solid #e8e8e8;
}
.kwTable td {
  padding: 10px 16px;
  line-height: 35px;
  border: 1px solid #e8e8e8;
}
.kwTable td.pd0 {
  padding: 0;
}
.kwTable th.f-tac {
  text-align: center;
}
.kwTable td.f-tac {
  text-align: center;
}
.kwTable tr:hover td {
  background: #e6f7ff;
}
.pages {
  text-align: right;
  margin: 16px 0;
}
</style>



