<template>
  <div>
    <div class="f-mask"></div>
    <div class="inner">
      <div class="header">
        <h3>配置信息</h3>
        <span class="close"
              @click="cancelClick()">
          <a-icon type="close" />
        </span>
      </div>
      <div class="edit-content">
        <a-table :columns="columns"
                 :data-source="tableData"
                 :row-selection="rowSelection"
                 :pagination="{total:total,pageSize:5}"
                 @change="childTableChange"
                 rowKey='id'
                 bordered>
          <template slot="status" slot-scope="status">
            <span>{{status==1?'可采集':'不可采集'}}</span>
          </template>
        </a-table>
      </div>
      <div class="btns">
        <a class="u-button"
           @click="cancelClick()">取消</a>
        <a class="u-button f-cblue"
           @click="sureClick()">确定</a>
      </div>
    </div>
  </div>
</template>

<script>
import $axios from '../../../../utils/request.js'
const columns = [
  {
    title: '模板名称',
    dataIndex: 'template_name',
    key: 'template_name',
    width: '20%'
  },
  {
    title: '路径',
    dataIndex: 'path',
    key: 'path',
    width: '70%'
  },
  {
    title: '状态',
    dataIndex: 'status',
    width: '10%',
    key: 'status',
    align: 'center',
    scopedSlots: { customRender: 'status' }
  }
]
const tableData = []

export default {
  name: 'ConfigPop',
  props: ['itemID'],
  data () {
    return {
      tableData,
      columns,
      pageSize: 5,
      page: 1,
      status: 1,
      current: 1,
      total: 0,
      selectedRowKeys: []
    }
  },
  mounted () {
    this.tableDataFn()
  },
  methods: {
    cancelClick () {
      this.$emit('ItemCancel')
    },
    sureClick () {
      if (this.selectedRowKeys.length !== 0) {
        this.importFn()
      }
      this.$emit('ItemCancel')
    },
    tableDataFn () { // 请求模板数据
      let url = `${this.baseURL}/template/select_remove?page=${this.page}&limit=${this.pageSize}&status=${this.status}&id=${this.itemID}`
      console.log($axios)
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          this.tableData = res.data.data
          this.total = res.data.count
        }
      })
    },
    importFn () {
      let url = `${this.baseURL}/key_select_template/add?key_id=${this.itemID}&template_id_list=${this.selectedRowKeys}`
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          this.tableData = res.data.data
          this.total = res.data.count
        }
      })
    },
    childTableChange (pagination) {
      this.page = pagination.current
      this.tableDataFn()
    },
    onSelectChange (selectedRowKeys) {
      // console.log('selectedRowKeys changed: ', selectedRowKeys)
      this.selectedRowKeys = selectedRowKeys.toString()
      console.log(this.selectedRowKeys)
    }
  },
  computed: {
    rowSelection () {
      return {
        onChange: this.onSelectChange,
        getCheckboxProps: record => ({
          props: {
            disabled: record.exist === 1 || record.status === 0// 如果这条数据中的exist的值为1，或status为0，则checkbook不能被选中
          }
        })
      }
    }
  }
}

</script>
<style scoped>
.edit-content >>> .ant-checkbox-disabled .ant-checkbox-inner {
  background: #a9a9a9;
}
.header {
  padding: 16px 24px;
  border-bottom: 1px solid #e8e8e8;
  position: relative;
}
.header .close {
  width: 56px;
  height: 56px;
  line-height: 56px;
  text-align: center;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
}
.header h3 {
  color: rgba(0, 0, 0, 0.85);
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
}
.edit-content {
  padding: 24px;
  padding-bottom: 50px;
}
.btns {
  padding: 10px 16px;
  text-align: right;
  background: transparent;
  border-top: 1px solid #e8e8e8;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
.btns .u-button {
  line-height: 30px;
  position: relative;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  background-image: none;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  height: 32px;
  padding: 0 15px;
  font-size: 14px;
  border-radius: 4px;
  color: rgba(0, 0, 0, 0.65);
  background-color: #fff;
  border: 1px solid #d9d9d9;
}
.btns .u-button.f-cblue {
  color: #fff;
  background-color: #1890ff;
  border-color: #1890ff;
  margin-left: 8px;
}
.f-mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
  filter: alpha(opacity=50);
}
.inner {
  min-width: 1000px;
  max-width: 1200px;
  height: 520px;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 9999;
  background: #fff;
  border-radius: 4px;
  transform: translate(-50%, -50%);
}
.kwTable {
  width: 100%;
}
.kwTable th {
  padding: 16px 16px;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  text-align: left;
  background: #fafafa;
  border: 1px solid #e8e8e8;
}
.kwTable td {
  padding: 10px 16px;
  line-height: 35px;
  border: 1px solid #e8e8e8;
}
.kwTable td.pd0 {
  padding: 0;
}
.kwTable tr:hover td {
  background: #e6f7ff;
}
</style>



